// Type your data here, it can be string, or any type of data, just write your variable
import {
  SET_DATA_LAPORAN_SETOR_ABU_COR_FAILED,
  SET_DATA_LAPORAN_SETOR_ABU_COR_SUCCESS,
} from "../actions/laporansetorabucor";
const initialState = {
  feedback: [],
  error: null,
  isEdit: false,
};

const laporansetorabucor = (state = initialState, action) => {
  switch (action.type) {
    case SET_DATA_LAPORAN_SETOR_ABU_COR_SUCCESS:
      return {
        ...state,
        feedback: action.payload.data,
      };
    case SET_DATA_LAPORAN_SETOR_ABU_COR_FAILED:
      return {
        ...state,
        error: action.payload.data,
      };
    default:
      return state;
  }
};

export default laporansetorabucor;
